<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  @keyframes toolbox{
    0%{
    top:0px;
  }
    50%{
      top:10px; 
    }
    100%{
      top:0px;
    }
  }
  @keyframes jumping{
    0%{
    top:0px;
    box-shadow: 0px 50px 40px rgba(black,0.2)
  }
    50%{
      top:10px; 
      box-shadow: 0px 10px 10px rgba(black,0.2)
    }
    100%{
      top:0px;
      box-shadow: 0px 50px 40px rgba(black,0.2)
    }
  }
  
  .headimg {
    width: 100%;
    height: auto;
    max-height: 90vh;
  }
  
  .icon{
    position: relative;
    width: 160px;
    height: 160px;
    background-color: #598392;
    border-radius: 50%;
}
span.highlight{
  color: #EEB868;
  font-weight: bold;
  opacity: 1!important;
}

.toolbox::before{
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  top: -40px;
  left: 50%;
  transform: translate(-50%);
  border-width: 20px;
  border-style: solid;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: rgba(229,222,165,0.25);
  border-left-color: transparent;
}

.toolbox{
  width: 90%;
  min-height: 150px;
  height: auto;
  background-color: rgba(229,222,165,0.25);
  border-radius: 5px;
  position: relative;
  margin: 20px 10px 20px 10px; 
  animation: toolbox 2s infinite 
}

.subtitle{
  font-size: 25px;
  color: #bbb;
}

#element li{
  display: flex;
  align-items: center;
  flex-direction: column;
}
#element p{
  font-size: 15px;
  padding: 10px;
  color: rgba(0,0,0,0.6);
}
#right .row .col-sm-8{
  display: flex;
  justify-content: flex-end;
}
#show h3{
  color: #6d787c;
  font-weight: 550;
  font-size: 25px;
}
#show p{ 
  text-align: center;
}
.sponsor li{
  transition: 0.5s;
  margin: 0px 10px 0px 10px ;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sponsor img{
  width: 120px;
  height: 120px;
  padding-bottom: 15px;
}

.sponsor p{
  background-color: rgba(89,131,146,0.5);
  color: #fff;
  text-align: center;
  width: 120px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
}
.sponsor li:hover{
  transform: scale(1.1);
}
.smallimg{
  width: 250px;
  height: 250px;
  margin: 10px 40px 10px 40px;
  animation: jumping 2s infinite ;
  position: relative;
}



@media (max-width: 768px) { 
  .smallimg{
    display: none;
  }
  .sponsor img{
    width: 60px;
    height: 60px;
  }
  .sponsor p{
    width: 60px;
    font-size: 10px;
  }
}
@media (max-width: 992px) { 
  .smallimg{
    display: none;
  }

}



</style>



