<template>
<div id="detectioninfo">
    <div class="container">
        <div class="row">
            <div class="col-sm-8">
                <p>{{detectioninfo.msg}} </p>
            </div>
        </div>
    </div>
    <div class="container">
        <div class="row">
            <div class="col-sm-12">
                <div class="row">
                    <div class="col-sm-4">
                        <img class="clock" src="/static/icon/.png">
                    </div>
                    <div class="col-sm-4">
                        <img class="eb" src="/static/icon/ 2.png">
                    </div>
                    <div class="col-sm-4">
                        <img class="form" src="/static/icon/2.png">
                    </div>
                </div>
                <router-link to="#" class="btn m10">Coming soon</router-link>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Detectionpage1 from '@/components/pages/Detectionpage1'
export default {
    name: "detectioninfo",
    data(){
        return{
            detectioninfo: {
                title: '',
                msg: ''
            }
        }
    }
}
</script>

<style>
#detectioninfo p{
    color: #fff;
}
.fas.fa-user-clock,.far.fa-list-alt,.fas.fa-cloud-rain{
    font-size: 100px;
    color: #fff;
    margin-top: 30px;
    margin-bottom: 50px;
}
.clock{
    width: 200px;
    height: 200px;
}
.form{
    width: 200px;
    height: 200px;
}
.eb{
    width: 200px;
    height: 200px;
}
.m10{
    margin-top: 30px;
}
</style>


