<template>
<div class="scene" data-1500="top: 100%;" data-2500="top: 0%; " data-3100="top: 0%; " data-4100="top: -100%; display: block;" data-4101="top: -100%; display: none;" style="top: 0%;display: block">
    <div class="section2">
        <div class="section2-inner">
            <img class="twostoneleft" src="static/fog/section2/2-stoneleft.png" data-2000="left: -100%; display: block;" data-2500="left: 0%; display: block;" style="left: -100%; display: block;">
            <img class="twostoneright" src="static/fog/section2/2-stoneright.png" data-2000="right: -100%; display: block;" data-2500="right: 3%; display: block;" style="right: -100%; display: block;">
            <div>
                <img class="twoboard" src="static/fog/section2/2-board.png" data-2500="left: 5%; display: block;" style="left: -100%; display: none;">
                <div class="twotext" data-2500="left: -100%; display: block;" data-3100="left: 15%; display: block;" style="left: 15%; display: block;">
                    <div v-if="step === 1" >
                        <p class="twotext1" style="left: 16%; display: block;"><highlight></highlight><br><br></p>
                        <img class="twoarrownext" src="static/fog/section2/arrownext.png" @click.prevent="next()" style="left: 55%; display: block;">
                    </div>
                    <div v-if="step === 2">
                        <p class="twotext2" style="left: 16%; display: block;"><highlight>.(Susan Forward)</highlight><br><highlight></highlight><br><highlight></highlight><br><highlight></highlight></p>
                        <img class="twoarrowpre" src="static/fog/section2/arrowpre.png" @click.prevent="prev()" style="left: 17%;display: block;">
                        <img class="twoarrownext" src="static/fog/section2/arrownext.png" @click.prevent="next()" style="left: 55%;display: block;">
                    </div>
                    <div v-if="step === 3">
                        <p class="twotext3" style="left: 16%; display: block;"><highlight></highlight><br><highlight></highlight><br></p>
                        <img class="twoarrowpre" src="static/fog/section2/arrowpre.png" @click.prevent="prev()" style="left: 17%;display: block;">
                        <img class="twoarrownext" src="static/fog/section2/arrownext.png" @click.prevent="next()" style="left: 55%;display: block;">
                    </div>
                    <div v-if="step === 4">
                        <p class="twotext4" style="left: 16%; display: block;"><br><br><br><highlight></highlight></p>
                        <img class="twoarrowpre" src="static/fog/section2/arrowpre.png" @click.prevent="prev()" style="left: 17%;display: block;">
                        <img class="twoarrownext" src="static/fog/section2/arrownext.png" @click.prevent="next()" style="left: 55%;display: block;">
                    </div>
                    <div v-if="step === 5">
                        <p class="twotext5" style="left: 20%; display: block;">  </p>
                        <img class="twoarrowpre" src="static/fog/section2/arrowpre.png" @click.prevent="prev()" style="left: 17%;display: block;">
                    </div>
                </div>
            </div>
            <img class="twograss" src="static/fog/section2/2-grass.png" data-2000="bottom: -100%; display: block;" data-2500="bottom: 15%; display: block;"  style="bottom: -100%; display: none;">
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            step:1,
        }
    },
    methods:{
        prev() {
            this.step--;
        },
        next() {
            this.step++;
        }
    }
}
</script>

<style>
@keyframes scale{
    0%{
        transform: scale(1)
    }
    50%{
        transform: scale(1.2) 
    }
    100%{
        transform: scale(1)
    }
}
.section2{
    height: 100%;
    background: #31664A;
}

.section2-inner{
    width: 1000px;
    height: 100%;
    margin: 0 auto;
    position: relative;
}
.twoboard{
    height: 75%;
    top: 10%;
}
.twograss{
    height: 10%;
    left: 10%;
}
.twostoneleft{
    height: 5%;
    top: 70%;
}
.twostoneright{
    height: 5%;
    top: 40%;
}
.twotext1,.twotext2,.twotext3,.twotext4,.twotext5{
    font-size: 25px;
    position: absolute;
    text-align: center;
}
.twotext1{
    top: 30%;
    color: #ddd;
}
.twotext2{
    height: 30%;
    top: 29%;
    color: #ddd;
}
.twotext3{
    height: 30%;
    top: 30%;
    color: #ddd;
}
.twotext4{
    height: 30%;
    top: 29%;
    color: #ddd;
}
.twotext5{
    height: 10%;
    top: 35%;
    color: #ddd;
}
.twoarrowpre{
    height: 6%;
    top: 58%;
    cursor: pointer;
    animation: scale 2s infinite;
}
.twoarrownext{
    height: 6%;
    top: 58%;
    cursor: pointer;
    animation: scale 2s infinite;
}
</style>


