<template>
<div>
    <img class="headimg" src="static/member/member.png">
    <section>
        <div class="container">
            <div class="row">
                <div class="col-sm-12 content">
                    <h2> {{info6.title}}
                    </h2>
                    <hr>
                    <p>{{info6.msg}}</p>
                    <ul>
                        <div class="row" id="member">
                            <div class="col-sm-12 col-md-6 col-lg-3" v-for="member in members" v-bind:key="member.id">
                                <li>
                                    <img class="memberphoto" :src="member.imgurl">
                                    <h3>{{member.name}}</h3>
                                    <p>{{member.opacity1}}<br/>{{member.opacity2}}</p>
                                    <ul class="list-inline.social-buttons">
                                        <li class="list-inline-item">
                                            <a :href="member.socialurl" target="_blank">
                                                <i class="fab fa-facebook"></i>
                                            </a>
                                        </li>
                                    </ul>
                                </li>
                            </div>
                        </div>
                    </ul>
                </div>
            </div>
        </div>
    </section>
    <section>
        <div class="container">
            <div class="row">
