                    <h2> {{info7.title}}
                    </h2>
                    <hr>
                    <p>{{info7.msg}}</p>
                    <ContactForm></ContactForm>
                </div>
            </div>
        </div>
    </section>
</div>

</template>

<script>
import ContactForm from './ContactForm.vue'
export default {
    name: 'About',
    components:{
        ContactForm: ContactForm,
    },
    data(){
        return{
            info6:{
                title: '',
                subtitle: '',
                msg: '105 ---'
            },
            info7:{
                title: '',
                subtitle: '',
                msg: ''
            },
            members:[
                {
                    id: 0,
                    imgurl: "static/member/-01.jpg",
                    name: "",
                    opacity1: "",
                    opacity2: "",
                    socialurl: "https://www.facebook.com/profile.php?id=100000328880237&ref=bookmarks"
                },
                {
                    id: 1,
                    imgurl: "static/member/-01.jpg",
                    name: "",
