                    socialurl: "https://www.facebook.com/profile.php?id=100001737366996",
                },
                {
                    id: 2,
                    imgurl: "static/member/-01.jpg",
                    name: " ",
                    opacity1: "",
                    opacity2: "",
                    socialurl: "https://www.facebook.com/coco.li.509"
                },
                {
                    id: 3,
                    imgurl: "static/member/-01.jpg",
                    name: "",
                    opacity1: "",
                    opacity2: "",
                    socialurl: "https://www.facebook.com/chen.xiu.1694",
                },
            ]
        }
    }

}
</script>

<style>
    .headimg {
        width: 100%;
        height: auto;
        max-height: 90vh;
    }
    .memberphoto{
        width: 150px;
        height: 150px;
        border-radius: 50%;
        margin-bottom: 20px;
        box-shadow: 10px 10px 20px rgba(0,0,0,0.3);
    }
    #member p{
        text-align: center;
    }
    
    .fa-facebook{
        color: #598392;
        font-size: 40px;
        padding-bottom: 20px;
    }
    #member .fa-facebook:hover{
        color: #EEB868;
        transform: scale(1.2);
        transition: 0.5s;
    }

    ul{
        padding-inline-start: 0px;
    }
</style>



